.footer.container {
    background-color: #4b341d;
    color: #9cbbaa;
    padding-block: 3em;
    display: grid; 
    grid-template-columns: 2fr 1fr 1fr;
    grid-gap: 1em;
    .footer-section{
        p{
            opacity: .9;
            margin-bottom: .5em;
        } 
        p.title {
            opacity: 1;
            font-size: 1.1em;
            font-weight: bold;
            margin-bottom: 1em;
        }
        a { 
            opacity: .9;
            margin-bottom: .5em;
            color: inherit; 
            display: block; 
        }
    }
}