:root {
    --background-color: #fff;
    --background-light: #fff;
    --shadow-color: rgba(0,0,0,0.2);
    --primary-color: rgb(155, 190, 168);
    --text-color: rgb(72, 48, 24);
    --text-light: #575757;
    --font-size: 16px; 
    --animation-speed: 1;
}
@function getAnimationDuration($duration: number) {
    @return calc($duration* var(--animation-speed));
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    font-size: var(--font-size);
    font-family: 'Merriweather', sans-serif;
    padding-top: 55px;
    background-color: var(--background-light);
}
a{
    text-decoration: none;
}
img {
    object-fit: cover;

}
.bt{
    border: none;
    outline: none;
    background-color: var(--primary-color);
    color: #fff;
    padding: .75em 1em;
    text-transform: uppercase;
    font-size: 1em;
    font-weight: bold;
    letter-spacing: 1px;
    border-radius: 3px;
    box-shadow: 0 2px 1px var(--shadow-color);
    &:hover{
        box-shadow: 0 4px 5px var(--shadow-color);
        cursor: pointer;
    }
}
.custom-img{
    height: 0;
    width: 100%;
    position: relative;
    img {
        position: absolute; 
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}
@import "partials/navbar";
.section{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 3vw; 
    &.d-block {
        display: block;
    }
    .title{
        color: var(--text-color);
        margin-bottom: .25em;
    }
    .info {
        color: var(--text-light);
        font-size: 1em;
        line-height: 1.7;
        margin-bottom: 1.5em;
        margin-top: 1.5em;
    }
    .col{
        width: 50%;
    }
}
@import "partials/hero-section";
@import "partials/skills-section";
@import "partials/quote-section";
@import "partials/chefs-section";

@import "partials/prev-searches";
@import "partials/recipesCard-section";

.options-container {
    box-shadow: 0 5px 10px var(--shadow-color);
    padding: 1em;
    border-radius: 5px;
    border-left: .5em solid var(--primary-color);
    background-color: var(--background-light);
    margin-top: 1em;
    display: flex;
    gap: 1em;
    .option {
        height: 3.5em;
        width: 3.5em;
        box-shadow: 0 3px 6px var(--shadow-color);
        border-radius: 5px;
        display: flex;
        justify-content: center;
        align-items: center;
        &.light{
            background-color: #fff;
        }
        &.dark{
            background-color: rgb(29,29,29);
        }
        .check {
            height: 2.5em;
            width: 2.5em;
            background-color: var(--primary-color);
            border-radius: 50%;
            text-align: center;
            line-height: 2.5em;
            svg{
                height: 1.5em;
                width: 1.5em;
            }
        }
    }
    .bt span{
        display: inline-block;
        margin-left: .5em;
    }
}
.d-block h2{
    font-size: 1.75em;
    color: var(--text-color)
}

@import "partials/footer-section";

.main{
    padding-block: 3vw;
}
.container {
    padding-inline: 12vw;
}
@import "partials/animation.scss";
@media screen and (max-width: 1280px) {
    .container{
        padding-inline: 18vw;
    }
}
@media screen and (max-width: 1050px) {
    .container{
        padding-inline: 12vw;
    }
}
@media screen and (max-width: 668px) { 
    body{
        padding-top: 75px;
    }
    .navbar{
        .nav-links{
            display: none;
        }
        .sidebar-bt{
            display: block;
        }
    }
    .section{
        .col{
            width: 100%;
            padding: 0 !important;
            text-align: center;
            margin-bottom: 2em;
        }
    }
    .skill-item{  
        display: inline-block;
        margin-right: .75em;
        margin-bottom: .75em;
    }
    .section.skills .bt{
        display: block;
        margin-left: 50%;
        transform: translateX(-50%)
    }
    .chef-card {
        box-shadow: 0 5px 10px var(--shadow-color);
    }
    .container{
        padding-inline: 6vw;
    }
    .footer {
        grid-template-columns: repeat(4, 1fr);
        .footer-section:nth-child(1) {
            grid-column: span 4;
        }
        .footer-section:nth-child(2), .footer-section:nth-child(3){
            grid-column: span 2;
        }
    }
}
@media screen and (max-width: 480px) { 
    body{
        padding-top: 75px;
        overflow-x: hidden; 
    }
    .container{
        padding-inline: 10vw; 
    }
}