.previous-searches{
    display: block; 
    h2 {
        color: var(--text-color);
        margin-bottom: .5em;
    }
    .prevs-container{
        .search-item {
            background-color: var(--primary-color);
            display: inline-block;
            padding: .5em 1em;
            margin: 0 1em 1em 0;
            border-radius: 30px;
            box-shadow: 0 3px 6px var(--shadow-color);
            transform: translateX(100%);
            opacity: 0;
            animation: getAnimationDuration(.5s) slideInRight forwards;
        }
    }
    .search-box{
        display: flex;
        align-items: center;
        justify-content: end;
        height: 35px;
        input, .bt {
            height: 35px;
            width: 40px;
            font-family: 'Merriweather', sans-serif;
        }
        input {
            min-width: 250px;
            padding: 0 .5em;
            border-radius: 5px 0 0 5px;
            outline: none;
            border: 1px solid #4d4d4d;
            
        }
        .bt{
            padding-block: 0;
            padding-inline: .25em;
            border-radius: 0 5px 5px 0; 
        }

        .s-icon{
            width: 27px; 
            cursor: pointer;
        }
    }
}
